              <path class="path4" d="M21.677 9.806c-0.569 0-1.032 0.463-1.032 1.032v14.452c0 0.569 0.463 1.032 1.032 1.032s1.032-0.463 1.032-1.032v-14.452c0-0.569-0.463-1.032-1.032-1.032z"></path>
            </symbol>
            <symbol id="icon-clock" viewBox="0 0 32 32">
              <title>clock</title>
              <path class="path1" d="M29.333 16c0-7.364-5.97-13.333-13.333-13.333s-13.333 5.97-13.333 13.333c0 7.364 5.97 13.333 13.333 13.333s13.333-5.97 13.333-13.333v0 0 0 0 0 0zM0 16c0-8.837 7.163-16 16-16s16 7.163 16 16c0 8.837-7.163 16-16 16s-16-7.163-16-16zM14.667 14.667v1.333h2.667v-10.667h-2.667v9.333zM24 18.667h1.333v-2.667h-10.667v2.667h9.333z"></path>
            </symbol>
            <symbol id="icon-question" viewBox="0 0 32 32">
              <title>question</title>
              <path class="path1" d="M16 2.56c7.411 0 13.44 6.029 13.44 13.44s-6.029 13.44-13.44 13.44c-7.411 0-13.44-6.029-13.44-13.44s6.029-13.44 13.44-13.44zM16 0c-8.822 0-16 7.178-16 16s7.178 16 16 16c8.822 0 16-7.178 16-16s-7.178-16-16-16z"></path>
              <path class="path2" d="M16 22.080c-1.059 0-1.92 0.861-1.92 1.92s0.861 1.92 1.92 1.92c1.059 0 1.92-0.861 1.92-1.92s-0.861-1.92-1.92-1.92z"></path>
              <path class="path3" d="M12.16 12.48c0.706 0 1.28-0.574 1.28-1.28 0-1.412 1.148-2.56 2.56-2.56s2.56 1.148 2.56 2.56c0 1.412-1.148 2.56-2.56 2.56-0.706 0-1.28 0.574-1.28 1.28v3.84c0 0.706 0.574 1.28 1.28 1.28s1.28-0.574 1.28-1.28v-2.723c2.224-0.575 3.84-2.616 3.84-4.957 0-2.823-2.297-5.12-5.12-5.12s-5.12 2.297-5.12 5.12c0 0.706 0.574 1.28 1.28 1.28z"></path>
            </symbol>
          </defs>
        </svg>
        <div class="container">
          <div class="checkout-addr">
            <div class="page-title-normal">
              <h2 class="page-title-h2"><span>check out</span></h2>
            </div>
            <!-- process step -->
            <div class="check-step">
              <ul>
                <li class="cur"><span>Confirm</span> address</li>
                <li><span>View your</span> order</li>
                <li><span>Make</span> payment</li>
                <li><span>Order</span> confirmation</li>
              </ul>
            </div>

            <!-- address list -->
            <div class="page-title-normal checkout-title">
              <h2><span>Shipping address</span></h2>
            </div>
            <div class="addr-list-wrap">
              <div class="addr-list">
                <ul>
                  <li v-for="(item,index) in addressListfilter" v-bind:class="{'check':addresscheck==index}" @click="selectaddress(item._id,index)">
                    <dl>
                      <dt>{{item.userName}}</dt>
                      <dd class="address">{{item.address}}</dd>
                      <dd class="tel">{{item.phoneNumber}}</dd>
                    </dl>
                    <div class="addr-opration addr-del">
                      <a href="javascript:;" class="addr-del-btn" @click="deladdress(item._id)">
                        <svg class="icon icon-del"><use xlink:href="#icon-del"></use></svg>
                      </a>
                    </div>
                    <div class="addr-opration addr-set-default" v-if="!item.isDefault">
                      <a href="javascript:;" class="addr-set-default-btn" @click="setDefault(item._id)"><i>是否设置为默认地址</i></a>
                    </div>
                    <div class="addr-opration addr-default" v-if="item.isDefault">默认地址</div>
                  </li>
                  <li class="addr-new">
                    <div class="add-new-inner" @click="addnewaddress">
                      <i class="icon-add">
                        <svg class="icon icon-add"><use xlink:href="#icon-add"></use></svg>
                      </i>
                      <p>Add new address</p>
                    </div>
                  </li>
                </ul>
              </div>

              <div class="shipping-addr-more" v-show="showmore">
                <a class="addr-more-btn up-down-btn" href="javascript:;" @click="setLimit" v-bind:class="{'open':expand}">
                  more
                  <i class="i-up-down ">
                    <i class="i-up-down-l"></i>
                    <i class="i-up-down-r"></i>
                  </i>
                </a>
              </div>
            </div>

            <!-- shipping method-->
            <div class="page-title-normal checkout-title">
              <h2><span>Shipping method</span></h2>
            </div>
            <div class="lemall-msg-info hidden">
              <span>The region you selected is not within our delivery area. Please select another shipping address within our delivery areas.</span>
            </div>
            <div class="shipping-method-wrap">
              <div class="shipping-method">
                <ul>
                  <li class="check">
                    <div class="name">Standard shipping</div>
                    <div class="price">Free</div>
                    <div class="shipping-tips">
                      <p>Once shipped，Order should arrive in the destination in 1-7 business days</p>
                    </div>
                  </li>
                </ul>
              </div>
            </div>
            <div class="next-btn-wrap">
              <a class="btn btn--m btn--red" @click="linkorder">Next</a>
            </div>
          </div>
        </div>
      </div>
         <Modal v-bind:modalShow="addressModal" v-on:close="closeModal">
           <p slot="message">
             <table class="addresstable">
                <tr>
                  <td>姓名</td>
                </tr>
               <tr>
                  <td><input type="text" class="username" placeholder="请输入姓名" v-model="username"></td>
               </tr>
           <tr>
             <td>地址</td>
           </tr>
             <tr>
                 <td><input type="text" class="address" placeholder="请输入地址" v-model="address"></td>
               </tr>
           <tr>
             <td>电话</td>
           </tr>
           <tr>
             <td><input type="text" class="phonenumber" placeholder="请输入电话号码" v-model="phonenumber"></td>
           </tr>
             </table>

           </p>
            <span slot="tipmsg" style="color:red;">{{errmsg}}</span>
           <div slot="btngroup">
             <a href="javascript:;" class="btn btn--m" @click="addressModal=false">取消</a>
             <a class="btn btn--m" @click="saveAddress">确定</a>
           </div>
        </Modal>
        <nav-footer></nav-footer>
    </div>
</template>
<style>
.addresstable{
    width:100%;
    border-collapse: collapse;
    table-layout: fixed;
  }
  .addresstable tr{
    text-align: left;
    height: 32px;
  }
  .addresstable input{
    height: 24px;
    line-height: 24px;
    width:96%;
  }

</style>
<script>
  import "@/assets/css/base.css"
  import "@/assets/css/checkout.css"
  import NavHeader from "@/views/navheader"
  import NavBreadcrumb from "@/views/breadcrumb"
  import NavFooter from "@/views/footer"
  import Modal from "@/components/Modal"
  import axios from "axios"
    export default{
        data(){
            return {
              addressModal:false,
              username:"",
              address:"",
              phonenumber:"",
              errmsg:'',
              addressList:[],
              addresscheck:'',
              expand:false,
              limit:3,
              addressid:''

            }
        },
        components: {
          NavHeader,
          NavBreadcrumb,
          NavFooter,
          Modal
        },
      mounted(){
        this.getaddressList();
      },
      computed:{
      addressListfilter(){

          return this.addressList.slice(0,this.limit)
        },
        showmore(){
          if(this.addressList.length>3){
            return true;
          }else{
            return false;
          }
        }

      },
      methods:{
        addnewaddress(){
          this.addressModal=true;
        },
        closeModal(){
          this.addressModal=false
        },
        getaddressList(){
          axios.post("/goodlist/addresslist").then((res) =>{
             var resData=res.data;
             if(resData.status==0){
               this.addressList=resData.result;
             }
          })
        },
        saveAddress(){
          axios.post("/goodlist/saveaddress",{
            username:this.username,
            address:this.address,
            phonenumber:this.phonenumber
          }).then((res)=>{
            var resData=res.data;
           if(resData.status==0){
             this.addressModal=false;
             this.getaddressList();
           }else{

             this.errmsg=resData.msg;
           }
          })
        },
        setDefault(addressid){

          axios.post("/goodlist/setdefault",{
            addressId:addressid
          }).then((res)=>{
            var resData=res.data;
          if(resData.status==0){
            this.getaddressList();
          }
          });
        },
        setLimit(){
           this.expand=!this.expand
           if(this.limit==3){
             this.limit=this.addressList.length
           }else{
             this.limit=3
           }

        },
        deladdress(addressid){
          axios.post("/goodlist/deladdress",{
            addressId:addressid
          }).then((res)=>{
            var resData=res.data;
          if(resData.status==0){
            this.getaddressList();
          }
        });
        },
       linkorder(){


          if(!this.addressId){
            this.addressId=this.addressList[0]._id;

          }
         var addressid= this.addressId;
         this.$router.push("/goods/order?addressid="+ addressid)
       },
        selectaddress(addressid,index){
          this.addresscheck=index;
          this.addressId=addressid;


        }


  }
    }
</script>



